<template>
    <div v-theme:columnn="'wihde'" id="show-blog">
        <h1>博客总览</h1>
        <input type="text" v-model="search" placeholder="搜索">
        <div v-for="blog in filteredBlogs" :key="blog.id" class="single-blog">
          <router-link v-bind:to="'/blog/' + blog.id"><h2 v-rainbow>{{ blog.title | to-uppercase}}</h2></router-link>
          <article>
            {{ blog.content | snippet}}
          </article>
        </div>
    </div>
  </template>
  
  <script>
import { computed } from 'vue';
import axios from 'axios';
  export default{
    name:'show-blog',
    data(){
        return{
          blogs:[],
          search:""
        }
    },
    created(){
        // this.$http.get('https://vuedemo-df8bb-default-rtdb.firebaseio.com/posts.json')
        axios.get('https://vuedemo-df8bb-default-rtdb.firebaseio.com/posts.json')
        .then((data) => {
            // return data.json();
            // console.log(data);
            // this.blogs = data.body.slice(0,10);
            //console.log(this.blogs)
            var blogArray = [];
            for(let key in data.data){
              data.data[key].id = key;
              blogArray.push(data.data[key]);
            }
            this.blogs = blogArray;
        })
        // .then(function(data){
        //   var blogArray = [];
        //   for(let key in data){
        //     // console.log("键值"+key);
        //     data[key].id = key;
        //     blogArray.push(data[key]);
        //   }
        //   // console.log(blogArray);
        //   this.blogs = blogArray;
        //   console.log(this.blogs)
        // })
    },
    computed:{
        filteredBlogs:function(){
            return this.blogs.filter((blog) => {
              return blog.title.match(this.search);
            })
        }
    },
    // 本地API
    filters:{
      "to-uppercase":function(value){
        return value.toUpperCase();
      },
      "snippet":function(value){
      return value.slice(0,100) + ".....";
      }
    },
    directives:{
      'rainbow':{
        bind(el,binding,vnode){
          el.style.color = "#"+Math.random().toString(16).slice(2,8);
        }
      }
    }
  }
  </script>
  
  <style>
    #show-blog{
      max-width: 800px;
      margin: 0 auto;
    }

    .single-blog{
      padding: 20px;
      margin: 20px 0;
      box-sizing: border-box;
      background: #eee;
    }

    #show-blog a{
      color: #444;
      text-decoration: none;
    }

    input[type="text"]{
      padding: 8px;
      width: 100%;
      box-sizing: border-box;
    }
  </style>
  


