<template>
    <div id="single-blog">
        <h1>{{ blog.title }}</h1>
        <article>{{ blog.content }}</article>
        <p>作者：{{ blog.author }}</p>
        <p>分类：</p>
        <ul>
            <li v-for="category in blog.categories" :key="category">
                {{ category }}
            </li>
        </ul>
        <button @click="deleteBlog()">删除</button>
        <router-link :to="'/edit/' + id">编辑</router-link>
    </div>
</template>

<script>
import axios from 'axios'
    export default{
        name:"single-blog",
        data(){
            return{
                id:this.$route.params.id,
                blog:{}
            }
        },
        created(){
            // this.$http.get('https://vuedemo-df8bb-default-rtdb.firebaseio.com/posts/'+this.id+".json" )
            axios.get('https://vuedemo-df8bb-default-rtdb.firebaseio.com/posts/'+this.id+'.json')
            .then((data) => {
                // console.log(data);
                // // this.blog = data.body;
                // return data.json();
                this.blog = data.data;
            })
            // .then(function(data){
            //     this.blog = data;
            // })
        },
        methods:{
            deleteBlog(){
                this.$http.delete("https://vuedemo-df8bb-default-rtdb.firebaseio.com/posts/"+this.id+".json")
                .then(response => {
                    this.$router.push({path:'/'})
                })
            }
        }
    }
</script>
    
<style>
    #single-blog{
        max-width: 960px;
        margin: 0 auto;
        padding: 20px;
        background: #eee;
        border: 1px dotted #aaa;
    }
    #single-blog a{
        text-decoration: none;
        color: #444;
        border: 1px solid rgb(29, 29, 29);
        padding: 3px;
        font-size: 10px;
    }
</style>


